import Vue from 'vue'
import Router from 'vue-router'
import EthLife from './components/EthLife.vue'
import Guide from './components/guide.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: EthLife
    },
    {
      path: '/guide',
      name: 'guide',
      component:   Guide
    },
    {
      path: '/:p',
      name: 'main',
      component: EthLife
    },

  ]
})