  methods: {
    langToggle(lan) {
      this.$i18n.locale = lan;
      localStorage.setItem("langs", lan);
      for( var i=0; i < this.items.length; i++) {
        if ( this.items[i]["lan"] === lan) {
          this.curr_lan = this.items[i]["lgName"];
        }
      }
    },

    langToggleMB(e) {
      this.$i18n.locale = e.target.value;
      localStorage.setItem("langs", e.target.value);
    },

    navClick() {
      console.log("nav")
      this.nav = !this.nav;
    },

    hideNavClick() {
      this.nav = !this.nav;
    },

    home() {
      this.$router.replace("/home");
    },

    guide() {
      this.$router.replace("/guide");
    },
  },
};
