    width: 43%;
  }

  .dailytopContainer div {
    display: flex;
    margin-bottom: 0.1622rem;
    justify-content: space-between;
  }

  .dailytopContainer div span:first-child,
  .dailytopContainer div span:last-child {
    font-weight: bold;
    font-size: 0.3243rem;
  }

  .dailytopContainer span:nth-child(2) {
