</style>
<script>
/* eslint-disable */

import { formatNum, toDec, fromDec, formatNumRound } from "./../decUtil";
import "element-ui/lib/theme-chalk/index.css";
import Proxy from "./../proxy.js";
import { ZERO_ADDR, DAY_SEC, FeeTo } from "../constants.js";
import { Message, MessageBox } from "element-ui";

export default {
  name: "contentDetails",
  data() {
    return {
      depositValue: "1",
      chooseLevel: 0,
      levels: [
        { level: 0, label: "1%", isCheck: true },
        { level: 1, label: "1.1%", isCheck: false },
        { level: 2, label: "1.2%", isCheck: false },
        { level: 3, label: "1.3%", isCheck: false },
        { level: 4, label: "1.4%", isCheck: false },
        { level: 5, label: "1.5%", isCheck: false },
      ],

      total_deposit: '-',
      contract_balance: '-',
      amount_withdrawed: '-',
      sponsor_pool: '-',
      lucky_pool: '-',
      fee_pool: '-',
      isFeeTo: false,
      needOpen: false,

      my_deposit: '-',
      countDownClock: "",
      dayCountDownClock: "",
      

      durtion: null,
      level: 0,
      daily_dividend: '-',
      daily_dividend_total: '-',
      locked_balance: '-',
      referral_bonus: '-',
      left_limit_balance: '-',
      matching_bonus: '-',
      top_bonus: '-',
      super_bonus: '-',
      lucky_bonus: '-',
      withdrawable_balance: '-',

      direct_iv_nums: '-',
      direct_iv_eths: '-',
      partners: '-',
      cycle: 0,
      min_eth: 0.1,
      max_eth: 20,

      tops: [],
    };
  },

  created() {
    this.getInvite();

    Proxy.initWeb3Account((web3, acc) => {
      this.account = acc;
      if (this.account.toLowerCase() == FeeTo.toLowerCase()) {
        this.isFeeTo = true;
      } else {
        this.isFeeTo = false;
      }

      this.uLink = process.env.VUE_APP_HOST + "p/" + this.account;

      this.uLinkShort =
        this.uLink.substr(0, 20) +
        "..." +
        this.uLink.substr(this.uLink.length - 4, this.uLink.length);
      this.web3 = web3;
      this.init();
    });
  },

  methods: {
    async init() {
      this.ethlife = await Proxy.getEthLife();
      console.log(this.ethlife.address);
      this.getPools();
      this.myOrder();
      this.myRefer();
    },

    joinChange(index) {
      this.levels.map((v, i) => {
        if (i == index) {
          v.isCheck = true;
        } else {
          v.isCheck = false;
        }
      });
    },

    fn(num) {
      return formatNum(num, 2);
    },

    getInvite() {
      let addr = this.$route.params.p;
      console.log("addr:" + addr);

      if (addr && addr.length === 42) {
        this.parentAddr = addr;
      } else {
        console.log("no parent");
      }
    },

    getPools() {
      this.ethlife.getPools({ from: this.account }).then((pools) => {
        this.total_deposit = formatNum(this.web3.utils.fromWei(pools[0]), 2);
        this.sponsor_pool = formatNum(this.web3.utils.fromWei(pools[1]), 2);
        this.lucky_pool = formatNum(this.web3.utils.fromWei(pools[2]), 2);
        this.fee_pool = formatNum(this.web3.utils.fromWei(pools[3]), 2);
      });

      this.web3.eth.getBalance(this.ethlife.address).then((b) => {
        this.contract_balance = formatNum(this.web3.utils.fromWei(b), 2);
      });

      this.web3.eth.getBalance(this.account).then((afterV) => {
        console.log(" afterV :" + this.web3.utils.fromWei(afterV));
      });
    },

    myRefer() {
      this.ethlife.referInfo({ from: this.account }).then((referInfo) => {
        this.direct_iv_nums = referInfo[0].toNumber();
        this.direct_iv_eths = this.web3.utils.fromWei(referInfo[1]);
        let p = referInfo[2].toNumber();
        console.log("partners:" + p);

        this.partners = p + this.direct_iv_nums;
      });
    },

    formatTstoDate(ts) {
      var joinTime = new Date(ts * 1000);
      return (
        joinTime.toLocaleDateString().replace(/\//g, "-") +
        " " +
        joinTime.toTimeString().substr(0, 8)
      );
    },

    formatSec(sec) {
      let m = parseInt(sec / 60);
      let h = parseInt(m / 60);
      let leftM = m - h * 60;
      let leftSec = sec - 60 * m;
      if (leftM < 10) {
        leftM = "0" + leftM;
      }

      if (h < 10) {
        h = "0" + h;
      }

      if (leftSec < 10) {
        leftSec = "0" + leftSec;
      }

      return h + ":" + leftM + ":" + leftSec;
    },

    countDown() {
      let clock = window.setInterval(() => {
        this.leftSec--;
        if (this.leftSec == 0) {
          this.leftSec = this.durtion;
        }

        var currTs = parseInt(new Date().getTime() / 1000);
        let times = parseInt((currTs - this.earnTs) / this.durtion);

        if(this.level > 0) {
          this.countDownClock = this.formatSec(this.leftSec);
        } 

        let passedDay = parseInt((this.durtion - this.leftSec) / DAY_SEC);
        // console.log("passedDay:" + passedDay + ", leftSec:" + this.leftSec + ", durtion:" + this.durtion + ", times:" + times);

        this.locked_balance = passedDay * parseFloat(this.daily_dividend);

        // this.daily_dividend_total = parseFloat(times * (this.level + 1) * this.daily_dividend);
        this.daily_dividend_total = parseFloat(formatNumRound((times * (this.level + 1) * this.daily_dividend), 3));

        // console.log(this.daily_dividend);
        // console.log(this.daily_dividend_total);
        
        
        if(this.level == 0) {
          this.dayLeftSec--;
          if (this.dayLeftSec == 0) {
            this.dayLeftSec = DAY_SEC;
          }
          this.dayCountDownClock = this.formatSec(this.dayLeftSec);
        }

      }, 1000);
    },

    myOrder() {
      this.ethlife.myOrder({ from: this.account }).then((o) => {
        this.my_deposit = this.web3.utils.fromWei(o[0]);
        let withdrawed = this.web3.utils.fromWei(o[1]);

        this.left_limit_balance = this.my_deposit * 2.5 - withdrawed;

        let level = o[2].toNumber();
        this.level = level;
        this.cycle = o[3].toNumber();
        if(parseFloat(this.my_deposit) >= 0.1) {
          this.min_eth = this.my_deposit
        }

        this.max_eth = 10 * 2 ** (this.cycle + 1)
        
        this.daily_dividend = parseFloat(this.my_deposit) * (0.01 + 0.001 * level)
        this.daily_dividend = parseFloat(formatNumRound(this.daily_dividend, 3))

        // this.locked_balance =
        //   parseFloat(this.my_deposit) * (0.01 + 0.001 * level) * (level + 1);
        // this.locked_balance = formatNumRound(this.locked_balance, 3);


        let durtion = (level + 1) * DAY_SEC;
        this.durtion = durtion;
        console.log("level:" + level + ", durtion:" + durtion + "daily:" + this.daily_dividend);
        var currTs = parseInt(new Date().getTime() / 1000);
        let earnTs = o[4].toNumber();
        this.earnTs = earnTs;

        if (earnTs > 0) {
          let times = parseInt((currTs - earnTs) / durtion);
          this.daily_dividend_total = parseFloat(formatNumRound((times * (this.level + 1) * this.daily_dividend), 3));

          let passedTs = currTs - earnTs - times * durtion;
          console.log("passedTs:" + passedTs + ", times:" + times);

          this.leftSec = durtion - passedTs;

          let days =  parseInt((currTs - earnTs) / DAY_SEC)
          console.log("days:" + days);

          
          this.dayLeftSec = DAY_SEC - (currTs - earnTs - days * DAY_SEC)
          if(this.level > 0) {
            this.countDownClock = this.formatSec(this.leftSec);
          }
          this.countDown();
        } else {
          this.daily_dividend_total = 0
          this.locked_balance = 0
        }
        

        this.lucky_bonus = this.web3.utils.fromWei(o[5]);
        this.super_bonus = this.web3.utils.fromWei(o[6]);
        this.referral_bonus = this.web3.utils.fromWei(o[7]);
        this.matching_bonus = this.web3.utils.fromWei(o[8]);
        this.top_bonus = this.web3.utils.fromWei(o[9]);
        let earn = this.web3.utils.fromWei(o[10]);
        let passDays = o[11].toNumber();

        console.log("earn:" + earn + ", passDays:" + passDays);

        this.withdrawable_balance =
          parseFloat(earn) + parseFloat(this.super_bonus);

        // this.withdrawable_balance = formatNumRound(this.withdrawable_balance, 3);

        this.top8();
      });
    },

    async join() {
      console.log(
        "join level:" +
          this.chooseLevel +
          ", " +
          this.parentAddr +
          ", value:" +
          this.depositValue
      );

      if (!this.depositValue) {
        Message.info({
          message: "Please input deposit value",
        });
        return;
      }

      let value = this.web3.utils.toWei(this.depositValue);
      let inviteAddr = this.parentAddr;
      if (!inviteAddr) {
        inviteAddr = ZERO_ADDR;
      }

      let level = 0;
      if (this.chooseLevel) {
        level = this.chooseLevel;
      }

      console.log("join inviteAddr:" + inviteAddr + " level:" + level);

      this.ethlife
        .join(inviteAddr, level, {
          from: this.account,
          value: value,
        })
        .on("error", (error) => {
          console.log("join error:", error);
        })
        .on("transactionHash", (hash) => {
          console.log("join transactionHash:", hash);
        })
        .then(() => {
          this.myOrder();
          this.getPools();
        })
        .catch((err) => {
          console.log("catch tx error:", err);
        });
    },

    wdFee() {
      this.ethlife.wdFee({ from: this.account }).then(() => {});
    },

    async withdraw() {
      let beforeV = await this.web3.eth.getBalance(this.account);
      console.log(" beforeV :" + this.web3.utils.fromWei(beforeV));

      console.log("withdraw");
      this.ethlife.wdEarn({ from: this.account }).then(() => {
        this.myOrder();
        this.getPools();
      });
    },

    async top8() {
      console.log("top8");
      var currTs = parseInt(new Date().getTime() / 1000);
      let day = parseInt((currTs - 21600) / DAY_SEC);

      let topAddrs = await this.ethlife.dayTopAddrs(day);
      console.log(topAddrs);

      let lastTopAddrs = await this.ethlife.dayTopAddrs(day-1)
      console.log("last tops:", lastTopAddrs);
      this.needOpen = false;
      if (lastTopAddrs.length >= 1) {
        this.needOpen = true;
      }

      let dayTopBonus = parseFloat(this.sponsor_pool) / 10;

      let len = topAddrs.length;
      this.tops = [];

      for (let i = 0; i < len; i++) {
        let dayV = await this.ethlife.dayReferValues(day, topAddrs[i]);

        let dayEth = this.web3.utils.fromWei(dayV);
        let percent = 0;

        if (i == 0) {
          percent = 0.4;
        } else if (i == 1) {
          percent = 0.2;
        } else if (i == 2) {
          percent = 0.12;
        } else if (i == 3) {
          percent = 0.08;
        } else if (i >= 4) {
          percent = 0.05;
        }

        let ibonus = dayTopBonus * percent;
        let addr =
          topAddrs[i].substring(0, 12) + "..." + topAddrs[i].substring(30, 42);

        this.tops.push({
          account: addr,
          ethVolume: dayEth,
          bonus: formatNumRound(ibonus, 3),
        });
      }
    },
  },
};
</script>


