<script>
/* eslint-disable */

import "element-ui/lib/theme-chalk/index.css";
import Proxy from "./../proxy.js";
import { Message, MessageBox } from "element-ui";

export default {
  name: "referralLink",
  data() {
    return {
      uLink: null,
    };
  },

  created() {
    Proxy.initWeb3Account((web3, acc) => {
      this.account = acc;
      this.uLink = process.env.VUE_APP_HOST + this.account;

      this.uLinkShort =
        this.uLink.substr(0, 20) +
        "..." +
        this.uLink.substr(this.uLink.length - 4, this.uLink.length);
    });
  },

  methods: {
    copyLink() {
      if (!navigator.clipboard) {
        var textArea = document.createElement("textarea");
        textArea.value = this.uLink;
        textArea.style.position = "fixed"; //avoid scrolling to bottom
        document.body.appendChild(textArea);
        textArea.focus();
        textArea.select();

        try {
          var successful = document.execCommand("copy");
          var msg = successful ? "successful" : "unsuccessful";
          console.log("Fallback: Copying text command was " + msg);

          Message.info({
            message: "Refer link is copyed",
          });
        } catch (err) {}

        document.body.removeChild(textArea);
      } else {
        navigator.clipboard.writeText(this.uLink).then(
          function () {
            Message.info({
              message: "Refer link is copyed",
            });
          },
          function (err) {
            Message.info({
              message: "Refer link copy fail",
            });
          }
        );
      }
    },
  },
};
</script>


